import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import CardApplicationAuto from '../../../../Views/CardApplicationAuto';
import Message from '../../../../Views/Messages/Message/Message';
import Pagination from '../../../../Views/Pagination/Pagination';
import { SafeAreaView, StyleSheet, Dimensions, ScrollView } from 'react-native-web';
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import CardApplicationAutoDuplicate from '../../../../Views/CardApplicationAutoDuplicate/CardApplicationAutoDuplicate';

const AutoDocsEditList = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        state?.textMessage
            ? setMessageInForm((c) => ({
                ...c,
                textMessage: state?.textMessage,
                colorMessage: state?.colorMessage ?? "#555050",
            }))
            : console.log("fake");
    }, [state?.textMessage, state?.colorMessage]);

    useEffect(() => {

        window.localStorage.setItem('numApplication', '')
        dispatch('setTextHeader', state?.name_button ?? 'РЕДАКТИРОВАТЬ ДОКУМЕНТЫ');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);
        tg.BackButton.onClick(() => navigate('/button_docs', {
            state: {
                name_button: state?.old_name_button ?? 'ФАНТИКИ'
            }
        }));

    }, []);

    useEffect(() => {

        const paramsList = {
            url: '/api_get_docs/',
            page: 1,
            page_size: 30,
            dataRequst: res => {

                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            }
        };
        dispatch('getListApplications', paramsList);

    }, []);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_get_docs/',
            page: page ?? 1,
            page_size: 30,
            setFieldValue: () => { },
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
            redirectTo: () => { }
        };
        dispatch('setDataCreateRequest', paramsList)
    }

    const styles = StyleSheet.create({
        conteiner: {
            width: '100%',
            position: 'relative',
            flex: 1,
        }
    })

    const margin = Dimensions.get('window').width - 320;

    return (
        <SafeAreaView style={[styles.container, {
            width: '100%',
            // marginBottom: 100,

        }]}>
            {
                listAplication.results.length ?
                    <ScrollView style={[styles.container, {
                        width: '100%',
                        // marginBottom: 100,
                        marginTop: margin / 4,
                        paddingLeft: margin / 2,
                        paddingRight: margin / 2,
                    }]}>
                        <Message
                            noicon
                            message={messageInForm.textMessage}
                            colorMessage={messageInForm.colorMessage}
                        />
                        {/* <CardApplicationAuto
                            listAplication={listAplication}
                            pathGoTo={'/button_docs/button_edit_doc_form'}
                        /> */}
                        <CardApplicationAutoDuplicate
                            listAplication={listAplication}
                            pathGoTo={`/button_my_advert/button_docs/button_edit_doc_form`}
                            click
                        />

                        {
                            listAplication.count > 30 ?
                                <Pagination
                                    data={listAplication.results}
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={changePaginationPage}
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                />
                                : null
                        }

                    </ScrollView>
                    : <Spinner
                        text={'У Вас нет пока заявок'}
                    />
            }

        </SafeAreaView>
    )
}
export default AutoDocsEditList;