import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Button from '../../../../Views/Button/Button';
import Offset from '../../../../Views/Offset';
import Input from '../../../../Views/Input/Input';
import Message from '../../../../Views/Messages/Message/Message';
import CardApplicationReview from '../../../../Views/CardApplicationReview/CardApplicationReview';
import { activeButtonBootomForConfirm, changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import Spinner from '../../../../Views/Spinner';
import CardApplicationReviewMembers from '../../../../Views/CardApplicationReviewMembers';
import Pagination from '../../../../Views/Pagination/Pagination';
import ErrorMessage from '../../../../Views/ErrorMessage/ErrorMessage';
import { Dimensions, SafeAreaView, ScrollView, StyleSheet } from 'react-native-web';
import { search } from '../../../../images/_icons/index'

const SeeReview = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [isLoadingGo, setIsLoadingGo] = useState(false);
    const [dataUser, setDataUser] = useState({});
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });

    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        dispatch('setTextHeader', state?.name_button ?? 'ПОСМОТРЕТЬ ОТЗЫВЫ');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);

        tg.BackButton.onClick(() => {
            navigate('/button_rating', {
                state: {
                    name_button: state?.old_name_button ?? 'РЕЙТИНГ И ОТЗЫВЫ'
                }
            })
        });

    }, [])

    const handlerSubmitSearch = data => {
        const params = {
            ...data,
            page: 1,
            page_size: 30,
            setIsLoading: setIsLoading,
            setIsLoadingGo: setIsLoadingGo,
            url: '/api_get_ratings/',
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) {
                    setListAplication(c => ({ ...c, results: [] }));
                    setDataUser({})
                    return true
                }

                setMessageInForm({
                    colorMessage: '#555050',
                    textMessage: `найдено ${res?.result?.count ?? 0} записей`
                });
                if (res?.result?.results?.length) {
                    const copyData = res?.result?.results.slice();
                    setListAplication(c => ({
                        ...c,
                        ...res.result,
                        results: [...copyData]
                    }))
                }
                setDataUser(res.result.feedback_user);
            },
        };
        dispatch('getContextSearch', params);
    }

    const changePaginationPage = ({ page, id_user }) => {
        const paramsList = {
            url: '/api_get_ratings/',
            page: page ?? 1,
            page_size: 30,
            username: dataUser.username,
            setIsLoading: setIsLoading,
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...res?.result?.results.slice()]
                }))
            },
        };
        dispatch('getContextSearch', paramsList);
        return false;
    }

    const handlerVirtualClick = (values, setFieldValue) => {
        return () => handlerSubmitSearch(values, setFieldValue)
    }

    const styles = StyleSheet.create({
        conteiner: {
            width: '100%',
            position: 'relative',
            flex: 1,
        },
        scroll: {
            alignItems: 'center',
        }
    })
    const margin = Dimensions.get('window').width - 320;

    return (
        <React.Fragment>
            <Formik
                initialValues={{
                    username: '',
                    page: 1,
                    page_size: 30
                }}
                onSubmit={handlerSubmitSearch}
            >
                {
                    ({ values, setFieldValue, handleChange }) => {
                        return (
                            <Form
                                autoComplete='none'
                            >
                                <SafeAreaView style={[styles.container, {
                                    marginTop: 16,
                                    width: '100%',
                                }]}>
                                    <Input
                                        value={values.username}
                                        placeholder={'введите имя пользователя'}
                                        name={'username'}
                                        id={1}
                                        icon={search}
                                        className={messageInForm.colorMessage === '#ff0000' && 'error-user'}
                                        onChange={e => {
                                            const value = e.target.value;
                                            setFieldValue('username', value)
                                            if (value.length > 3) {
                                                activeButtonBootomForConfirm(tg, { ...values, username: value }, 'searchReview', 'посмотреть')
                                            } else {
                                                setDataButtonBottonTrigger(false, tg)
                                            }
                                        }
                                        }
                                    />
                                    <Message
                                        noicon
                                        message={messageInForm.textMessage}
                                        colorMessage={messageInForm.colorMessage}
                                    />
                                    <button
                                        className='btn-click'
                                        onClick={() => handlerVirtualClick(values, setFieldValue)}
                                    ></button>
                                    {
                                        !!!tg.initDataUnsafe?.query_id ?
                                            <Offset
                                                mt={10}
                                            >
                                                <Button
                                                    disabled={!(values.username)}
                                                    type={'submit'}
                                                    addClass={'form-submit__btn'}
                                                    active={values.username}
                                                >
                                                    <span>
                                                        <span>посмотреть</span>
                                                    </span>
                                                </Button>
                                            </Offset>
                                            : null
                                    }
                                </SafeAreaView>
                            </Form>
                        )
                    }
                }
            </Formik>
            <SafeAreaView style={[styles.container, {
                // backgroundColor: schemaColorList.colorFoneBackgroundElement,
                width: '100%',
                // marginBottom: 100,
            }]}>
                {
                    listAplication.results.length ?
                        <ScrollView
                            contentContainerStyle={styles.scroll}
                            style={{
                                marginTop: margin / 4,
                                paddingLeft: margin / 2,
                                paddingRight: margin / 2,
                                width: '100%',
                                flex: 1,
                            }}
                        >
                            {
                                Object.keys(dataUser).length > 0 ?
                                    <CardApplicationReviewMembers
                                        fullCard
                                        listAplication={{ results: [dataUser] }}
                                    />
                                    : null
                            }
                            <Offset mb={30}>
                                <div style={{
                                    color: schemaColorList.colorText,
                                    width: '100%'
                                }}>найдено {listAplication?.count} отзыв(ов)</div>

                                <CardApplicationReview
                                    listAplication={listAplication}
                                    tg={tg}
                                />
                                {
                                    listAplication.count > 30 ?
                                        <Pagination
                                            allCount={listAplication.count}
                                            count={30}
                                            handlerChangePaginations={changePaginationPage}
                                            currentPage={Number(listAplication.current_page)}
                                            location={'center'}
                                        />
                                        : null
                                }

                            </Offset>
                        </ScrollView>
                        : isLoadingGo ?
                            <Spinner
                                text={`Результат поиска: ${listAplication?.results.length}`}
                            />
                            : null
                }
            </SafeAreaView>
        </React.Fragment>
    )
}
export default SeeReview;