import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { changeStateBackButton, handlerWarningInfoMessageResponse, serialaizerFiltersMenu, serialaizerMyProfilesMenu, setDataButtonBotton, setDataButtonBottonTrigger, smoothTop } from "../../helpers";
import Icon from "../../Views/Icon/Icon";
import Block from "../../Views/ListMainButton/DetaliBlock";
import Message from "../../Views/Messages/Message/Message";
import Spinner from "../../Views/Spinner";

import style from "./styles/myprofile.module.scss";
import NativeListMenu from "../../Views/NativeListMenu/NativeListMenu";

const MyProfile = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [contextPage, setContextPage] = useState({ message: "", buttons: [] });
  const { dispatch, context, tg, schemaColorList } = useStoreon("context", "tg", 'schemaColorList');
  const { buttons } = contextPage;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    setContextPage((c) => ({ ...c, ...context.children }));
  }, [context?.children]);

  useEffect(() => {
    state?.textMessage
      ? setMessageInForm((c) => ({
        ...c,
        textMessage: state?.textMessage,
        colorMessage: state?.colorMessage ?? "#555050",
      }))
      : contextPage.message
        ? setMessageInForm((c) => ({
          ...c,
          textMessage: contextPage.message,
        }))
        : console.log("fake");
  }, [contextPage.message, state?.textMessage]);

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? 'ПОДПИСКА');
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() => navigate("/"));

  }, []);

  useEffect(() => {
    const params = {
      url: "/api_get_context_profile/",
      page_id: state?.action_page_id ?? 31,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
      },
    };
    dispatch("getContext", params);

  }, []);

  const handlerChangeScreen = (url) => {
    navigate(url)
  }

  return (
    <React.Fragment>
      {buttons?.length ? (
        <React.Fragment>
          <Message
            noicon
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          <NativeListMenu
            list={serialaizerMyProfilesMenu(buttons)}
            handlerChangeScreen={handlerChangeScreen}
            colorFoneCardInfo={schemaColorList.colorFoneCardInfo}
            colorText={schemaColorList.colorText}
            bgColor={schemaColorList.colorFoneBackground}
            toggleBgColor={schemaColorList.toggleBgColor}
            colorFoneBackground={schemaColorList.colorFoneBackground}
            colorFoneBackgroundElement={schemaColorList.colorFoneBackgroundElement}
          />

        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default MyProfile;
