import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import { activeButtonBootomForConfirm, changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import Button from '../../../../Views/Button/Button';
import Input from '../../../../Views/Input/Input';
import Offset from '../../../../Views/Offset';
import RaitingContainer from '../../../../Views/Raiting/RaitingContainer';
import TextArea from '../../../../Views/TextArea/TextArea';
import Message from '../../../../Views/Messages/Message/Message';
import { Dimensions, SafeAreaView, StyleSheet } from 'react-native-web';
import Label from '../../../../Views/Label/Label';

const CreateReview = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: "",
        colorMessage: "",
    });

    useEffect(() => {
        dispatch('setTextHeader', state?.name_button ?? 'НАПИСАТЬ ОТЗЫВ');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);
        tg.BackButton.onClick(() => navigate('/button_rating', {
            state: {
                name_button: state?.old_name_button ?? 'РЕЙТИНГ И ОТЗЫВЫ'
            }
        }));

    }, []);

    const handlerSubmitChooseAggrigate = (data, { setFieldValue }) => {
        const params = {
            ...data,
            url: '/api_create_rating/',
            setFieldValue: setFieldValue,
            dataRequst: (res) => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
            },
            redirectTo: (res) => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const timer = setTimeout(() => {
                    navigate('/button_rating', {
                        state: {
                            ...state,
                            textMessage: res?.info?.message,
                            colorMessage: res?.info?.status ? "#555050" : "#ff0000",
                            name_button: state?.old_name_button ?? "СОЗДАТЬ ЗАПРОС",
                        },
                    });
                    return () => clearTimeout(timer);
                }, 100);
            }
        }
        dispatch('addReview', params);
        return false;
    }

    const handlerVirtualClick = (values, setFieldValue) => {
        return () => handlerSubmitChooseAggrigate(values, setFieldValue)
    }

    const styles = StyleSheet.create({
        conteiner: {
            width: '100%',
            position: 'relative',
            flex: 1,
        },
    })
    const margin = Dimensions.get('window').width - 320;

    return (
        <React.Fragment>
            <Formik
                initialValues={{
                    text: '',
                    rating: 0,
                    page: 1,
                    username: '',
                    page_size: 30,
                }}
                onSubmit={handlerSubmitChooseAggrigate}
            >
                {
                    ({ values, setFieldValue }) => {

                        const handlerChangeReviewStar = (data, values) => {
                            setFieldValue('rating', data.target.getAttribute('value'));
                            activeButtonBootomForConfirm(tg, { ...values, rating: data.target.getAttribute('value') }, 'review', 'Опубликовать');
                        }

                        return (
                            <React.Fragment>
                                <Form
                                    style={{
                                        width: '100%',
                                    }}
                                >
                                    {/* Поле для ввода юзернейма */}
                                    <SafeAreaView style={[styles.container, {
                                        backgroundColor: schemaColorList.colorFoneBackgroundElement,
                                        marginTop: 16,
                                        paddingLeft: margin / 2,
                                        paddingRight: margin / 2,
                                        // width: '100%',
                                    }]}>
                                        <Label>Имя пользователя</Label>
                                        <Input
                                            value={values.username}
                                            placeholder={'введите имя пользователя'}
                                            name={'username'}
                                            className={messageInForm.colorMessage === '#ff0000' && 'error-user'}
                                            id={1}
                                            onChange={e => {
                                                const value = e.target.value;
                                                setFieldValue('username', value)
                                                if (value.length > 3) {
                                                    activeButtonBootomForConfirm(tg, { ...values, username: value }, 'review', 'Опубликовать');
                                                } else {
                                                    setDataButtonBottonTrigger(false, tg)
                                                }
                                            }
                                            }
                                        />
                                        <Message
                                            noicon
                                            message={messageInForm.textMessage}
                                            colorMessage={messageInForm.colorMessage}
                                        />
                                        <Offset position={'center'}>
                                            <Label>Поставьте оценку</Label>
                                            <RaitingContainer
                                                max={5}
                                                value={values.rating}
                                                onChange={e => handlerChangeReviewStar(e, values)}
                                                sizeStarHeight={30}
                                                sizeStarWidth={30}
                                            />
                                        </Offset>
                                        {/* Ниже текстовое поле,для самого отзывы */}
                                        <TextArea
                                            className={'textarea-application'}
                                            value={values.text}
                                            name={'text'}
                                            placeholder={'Напишите отзыв'}
                                            height={110}
                                            onChange={(e) => {
                                                const value = e.target.value;
                                                setFieldValue('text', value);
                                                if (!value) return setDataButtonBottonTrigger(false, tg);
                                                activeButtonBootomForConfirm(tg, { ...values, text: value }, 'review', 'Опубликовать')
                                            }}
                                        />
                                        <button
                                            className='btn-click'
                                            onClick={() => handlerVirtualClick(values, setFieldValue)}
                                        ></button>

                                        {
                                            !!!tg.initDataUnsafe?.query_id ?
                                                <Offset
                                                    mt={40}
                                                >
                                                    <Button
                                                        disabled={!(values.username && values.text && values.rating)}
                                                        type={'submit'}
                                                        addClass={'form-submit__btn'}
                                                        active={values.username && values.text && values.rating}
                                                    >
                                                        <span>
                                                            <span>Опубликовать</span>
                                                        </span>
                                                    </Button>
                                                </Offset>
                                                : null
                                        }
                                    </SafeAreaView>
                                </Form>
                            </React.Fragment>
                        )
                    }
                }
            </Formik>
        </React.Fragment>
    )
}
export default CreateReview;