import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import {
  activeButtonBootomForConfirm,
  changeStateBackButton,
  getOptions,
  handlerWarningInfoMessageResponse,
  setDataButtonBottonTrigger,
} from '../../../../helpers';
import Button from '../../../../Views/Button/Button';
import Message from '../../../../Views/Messages/Message/Message';
import Offset from '../../../../Views/Offset';
import Select from '../../../../Views/Select';
import SelectDesktop from '../../../../Views/Select/SelectDesktop';
import Spinner from '../../../../Views/Spinner';
import TextArea from '../../../../Views/TextArea/TextArea';
import { Dimensions, SafeAreaView, StyleSheet } from 'react-native-web';
import Label from '../../../../Views/Label/Label';
import NativeSelect from '../../../../Views/NativeSelect/NativeSelect';

const AggregateRequestRespair = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
  const [dataRequstCountry, setDataRequestCountry] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: '',
    colorMessage: '',
  });

  useEffect(() => {
    dispatch('setTextHeader', state?.name_button ?? 'ЗАПРОС НА РЕМОНТ');
    dispatch('setActiveHeaderText', true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate('/button_make_request', {
        state: {
          name_button: state?.old_name_button ?? 'СОЗДАТЬ ЗАПРОС',
        },
      })
    );
  }, []);

  useEffect(() => {
    const params = {
      url: '/api_get_countrys/',
      page_id: state?.action_page_id ?? 2,
      dataRequst: setDataRequestCountry,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        if (res?.result.length) {
          const copyData = res?.result?.slice();
          setDataRequestCountry((c) => [
            ...JSON.parse(JSON.stringify(copyData)),
          ]);
        }
      },
    };
    dispatch('getContext', params);

  }, []);

  const handlerSubmitChooseAggrigate = (data, setFieldValue) => {
    const params = {
      ...data,
      url: '/api_create_request/',
      setFieldValue: setFieldValue,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
      },
      redirectTo: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        dispatch('openModal', {
          // content = 'text'
          // show = boolean
          // contentBtn = 'надпись кнопки закрытия'
          // error = boolean image
          show: res?.result?.info?.status,
          content: res?.result?.info?.message,
          contentBtn: 'ok',
          error: !res?.result?.info?.status,
          path: '/button_make_request',
          state: {
            ...state,
            // textMessage: res?.result?.info.message,
            // colorMessage: res?.result?.info.status,
            name_button: state?.old_name_button ?? 'СОЗДАТЬ ЗАПРОС',
          },
        })
        // navigate('/button_make_request', {
        //   state: {
        //     ...state,
        //     textMessage: res?.result?.info.message,
        //     colorMessage: res?.result?.info.status ? '#555050' : '#ff0000',
        //     name_button: state?.old_name_button ?? 'СОЗДАТЬ ЗАПРОС',
        //   },
        // })
      }
    };
    dispatch('setDataCreateRequest', params);
    return false;
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitChooseAggrigate(values, setFieldValue);
  };
  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })
  const margin = Dimensions.get('window').width - 320;
  return (
    <SafeAreaView style={[styles.container, {
      backgroundColor: schemaColorList.colorFoneBackgroundElement,
      width: '100%',
      marginTop: margin / 4,
    }]}>
      {
        dataRequstCountry.length ? (
          <React.Fragment>
            <Message
              message={messageInForm.textMessage}
              colorMessage={messageInForm.colorMessage}
            />
            <Formik
              initialValues={{
                optionsCountry: dataRequstCountry,
                optionsCities: [],
                activeCity: false,
                type: state?.type ?? 'respair',
                country_id: null,
                city_id: null,
                text: '',
              }}
              onSubmit={handlerSubmitChooseAggrigate}
            >
              {({ values, setFieldValue }) => {
                const handlerChangeDataRequest = (data) => {
                  let options = getOptions(data);
                  return options;
                };

                return (
                  <Form>
                    <SafeAreaView style={[styles.container, {
                      width: '100%',
                      paddingLeft: margin / 2,
                      paddingRight: margin / 2,
                    }]}>

                      <Label>Страна</Label>
                      <NativeSelect
                        data={getOptions(values.optionsCountry)}
                        enabled={true}
                        selectedValue={values.country_id}
                        placeholder={'Выберите страну'}
                        onChange={(value) => {
                          console.log({ value })
                          const sities = handlerChangeDataRequest(values.optionsCountry.map((el) => {
                            if (el.id === +value) return el.citys;
                          }).filter((el) => el !== undefined)[0]
                          );
                          setFieldValue('country_id', value);
                          setFieldValue('optionsCities', sities);
                          setFieldValue('city_id', null);
                          setDataButtonBottonTrigger(false, tg);
                        }}
                      />
                      <Offset mt={16} />
                      <Label>Город</Label>
                      <NativeSelect
                        data={values.optionsCities}
                        enabled={values.optionsCities.length}
                        selectedValue={values.city_id}
                        placeholder={'Выберите город'}
                        onChange={(value) => {
                          console.log({ value })
                          setFieldValue('city_id', value);
                          activeButtonBootomForConfirm(tg, { ...values, city_id: value }, 'country-text', 'Опубликовать');
                        }}
                      />
                      <Offset mt={16} />

                      <TextArea
                        className={'textarea-application'}
                        value={values.text}
                        name={'text'}
                        placeholder={'Введите данные для запроса'}
                        height={110}
                        onChange={(e) => {
                          const value = e.target.value;
                          setFieldValue('text', value);
                          if (!value)
                            return setDataButtonBottonTrigger(false, tg);
                          activeButtonBootomForConfirm(tg, { ...values, text: value }, 'country-text', 'Опубликовать');
                        }}
                      />

                      <button
                        className='btn-click'
                        onClick={() =>
                          handlerVirtualClick(values, setFieldValue)
                        }
                      ></button>
                      {!!!tg.initDataUnsafe?.query_id ? (
                        <Button
                          disabled={
                            !(
                              values.text &&
                              values.country_id &&
                              values.city_id
                            )
                          }
                          type={'submit'}
                          addClass={'form-submit__btn'}
                          active={
                            values.text && values.country_id && values.city_id
                          }
                        >
                          <span>
                            <span>создать</span>
                          </span>
                        </Button>
                      ) : null}
                    </SafeAreaView>
                  </Form>
                );
              }}
            </Formik>
          </React.Fragment>
        ) : (
          <Spinner />
        )
      }
    </SafeAreaView>
  );
};
export default AggregateRequestRespair;
