import React from "react";
import { SafeAreaView, FlatList, StyleSheet } from "react-native-web";
import ItemTopReview from "./Detail/ItemTopReview.js";

const CardApplicationTopReview = ({
    listAplication,
    handlerChangeScreen,
    colorFoneBackgroundElement,
    colorText,
}) => {

    return(
                    <SafeAreaView
                        style={styles.mainContainerList}
                    >   
                        <FlatList
                            data={listAplication.results}
                            renderItem={({ item, }) => <ItemTopReview item={item} handlerChangeScreen={handlerChangeScreen} colorText ={colorText} colorFoneBackgroundElement ={colorFoneBackgroundElement}/>}
                            keyExtractor={item => item.id}
                        />
                    </SafeAreaView>
    )
}
export default CardApplicationTopReview;
