import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import { activeButtonBootomForConfirm, changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import Button from '../../../../Views/Button/Button';
import Message from '../../../../Views/Messages/Message/Message';
import Offset from '../../../../Views/Offset';
import PreviewImages from '../../../../Views/PreviewImages/PreviewImages';
import Spinner from '../../../../Views/Spinner';
import TextArea from '../../../../Views/TextArea/TextArea';
import FormUploadImage from '../../../../Views/UploadImage/FormUploadImage';
import { SafeAreaView, StyleSheet, Dimensions, ScrollView } from 'react-native-web';


const AutoDocsEditForm = (

) => {
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const { state } = useLocation();
    const navigate = useNavigate();
    const numApplicationStorege = +window.localStorage.getItem('numApplication');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        dispatch('setTextHeader', `Объявление номер №${state?.application ?? numApplicationStorege}`);
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);
        tg.BackButton.onClick(() => navigate('/button_docs/button_edit_doc', {
            state: {
                name_button: state?.old_name_button ?? 'РЕДАКТИРОВАТЬ ДОКУМЕНТЫ'
            }
        }));

    }, [])

    useEffect(() => {

        const paramsList = {
            url: '/api_get_docs/',
            page: 1,
            page_size: 1111130,
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const copyData = res?.result?.results.slice().filter(el => el.id === (state?.application ?? numApplicationStorege));
                console.log({ copyData })
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
        };
        dispatch('getListApplications', paramsList)
    }, [])

    const handlerSubmitChange = (data, setFieldValue) => {
        delete data['type']
        // delete data['brand_id']
        const params = {
            ...data,
            confirmShow: true,
            url: '/api_edit_doc/',
            setFieldValue: () => { },
            redirectTo: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                navigate('/button_docs/button_edit_doc', {
                    state: {
                        ...state,
                        textMessage: res?.result?.info?.message,
                        colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
                        name_button: state?.old_name_button ?? "РЕДАКТИРОВАТЬ ДОКУМЕНТЫ",
                    },
                })
            }
        }
        dispatch('setDataEditRequest', params);
        return false;
    }

    const deleteImage = id => {
        try {
            const params = {
                parsing_image_id: id,
                url: '/api_delete_doc_image/',
                dataRequst: res => {
                    const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                    if (isWarning) return;
                    const copyDataImages = res?.result?.results.images.slice();
                    setListAplication(c => (
                        {
                            ...c,
                            results: [
                                {
                                    ...c.results[0],
                                    images: copyDataImages
                                },
                            ]
                        }))
                },
            }
            dispatch('deleteApplicationId', params)
            return true
        } catch (err) {
            console.log('delete application ERROR', err)
        }
    }

    const handlerVirtualClick = (values, setFieldValue) => {
        return () => handlerSubmitChange(values, setFieldValue)
    }

    const styles = StyleSheet.create({
        conteiner: {
            width: '100%',
            position: 'relative',
            flex: 1,
        }
    })

    const margin = Dimensions.get('window').width - 320;


    return (
        <SafeAreaView style={[styles.container]}>
            {
                listAplication.results.length && listAplication.results[0]?.id ?
                    <SafeAreaView
                        style={[styles.container, {
                            backgroundColor: schemaColorList.colorFoneBackgroundElement,
                            width: '100%',
                            // marginBottom: 100,           
                            marginTop: margin / 4,
                            paddingLeft: margin / 2,
                            paddingRight: margin / 2,
                        }]}
                    >
                        <Message
                            noicon
                            message={messageInForm.textMessage}
                            colorMessage={messageInForm.colorMessage}
                        />

                        <Formik
                            initialValues={
                                {
                                    text: listAplication?.results[0]?.text,
                                    uploadFiles: listAplication.results[0].images,
                                    page: 1,
                                    page_size: 30,
                                    parsing_id: state?.application ?? numApplicationStorege
                                }
                            }
                            onSubmit={handlerSubmitChange}
                        >
                            {
                                ({ values, setFieldValue }) => {

                                    return (
                                        <Form
                                            style={{
                                                width: '100%',
                                                position: 'relative',
                                            }}
                                        >
                                            <ScrollView
                                                style={[styles.container, {
                                                    backgroundColor: schemaColorList.colorFoneBackgroundElement,
                                                    width: '100%',
                                                    // marginBottom: 100,           
                                                    marginTop: margin / 4,
                                                    paddingLeft: margin / 2,
                                                    paddingRight: margin / 2,
                                                }]}
                                            >

                                                <Offset mt={20} />
                                                <TextArea
                                                    className={'textarea-application'}
                                                    value={values.text}
                                                    name={'text'}
                                                    placeholder={'Введите данные для запроса'}
                                                    height={110}
                                                    onChange={(e) => {
                                                        const value = e.target.value;
                                                        setFieldValue('text', value);
                                                        if (!value) return setDataButtonBottonTrigger(false, tg);
                                                        activeButtonBootomForConfirm(tg, { ...values, text: value }, 'text', 'Опубликовать');
                                                    }}
                                                />

                                                <PreviewImages
                                                    list={listAplication.results[0].images}
                                                    deleteImage={deleteImage}
                                                />

                                                <FormUploadImage
                                                    values={values}
                                                    setFieldValue={setFieldValue}
                                                    label={'прикрепить изображение'}
                                                    callback={
                                                        files => activeButtonBootomForConfirm(tg, { ...values, uploadFiles: files }, 'change_file', 'Опубликовать')
                                                    }
                                                />

                                                <button
                                                    className='btn-click'
                                                    onClick={() => handlerVirtualClick(values, setFieldValue)}
                                                ></button>
                                                {
                                                    !!!tg.initDataUnsafe?.query_id ?
                                                        <Offset
                                                            mb={20}
                                                        >
                                                            <Button
                                                                disabled={!(values.text)}
                                                                type={'submit'}
                                                                addClass={'form-submit__btn'}
                                                                active={!!values.text}
                                                            >
                                                                <span>
                                                                    <span>Подтвердить изменения</span>
                                                                </span>
                                                            </Button>
                                                        </Offset>
                                                        : null
                                                }
                                            </ScrollView>
                                        </Form>
                                    )
                                }
                            }
                        </Formik>

                    </SafeAreaView>
                    : <Spinner />
            }
        </SafeAreaView>
    )
}
export default AutoDocsEditForm;