import React from "react";
import Icon from "../Icon/Icon";

import style from './styles/cardApplicationAutoDuplicate.module.scss';
import { useStoreon } from "storeon/react";
import ViewsImage from "../ViewsImage";

import { copyImage } from '../../images/_icons/index';
import { useNavigate } from "react-router-dom";
import classNames from "classnames";
import { checkTrue1 } from "../../images";

const CardApplicationAutoDuplicate = ({
    listAplication,
    click = false,
    clickDel = false,
    pathGoTo = () => { },
    handlerInCatalogApplication = () => { },
    handlerChangeApplication = () => { },
    handlerDeleteApplication = () => { },
    handlerCopiesApplication,
    selectId,
    handlerChooseItem,

}) => {
    const { dispatch } = useStoreon();
    const navigate = useNavigate();
    const openModalImage = (images) => {

        dispatch('openModal', {
            show: true,
            content: <ViewsImage
                images={images}
            />

        })
    }

    const goTo = url => {
        navigate(url)
    }

    return (
        <div>
            {
                listAplication.results.length &&
                listAplication.results.map( el => {
                    return (
                        <div 
                            key={el.id} 
                            className={style['card-app__container']}
                            onClick={()=>{
                                if(click){
                                    window.localStorage.setItem('numApplication', el.id)
                                    goTo(`${pathGoTo}`)
                                }
                                if(clickDel){
                                    handlerChooseItem(el.id)
                                }
                            }}
                        >
                            {
                                // true?
                                selectId && selectId.includes(el.id) ?
                                    <Icon
                                        image={checkTrue1}
                                        width={70}
                                        height={60}
                                        className={style['card-application-auto-duplicate__status-icon']}
                                    />
                                    : null
                            }
                            {   
                                el?.type?
                                    <div className={style['card-app__info-block']}>
                                        <div className={style['card-app__where-app']}>
                                            {el.type.name}
                                        </div>
                                        <div className={style['card-app__container-duplicat']}>
                                            <button
                                                className={style['card-app__duplicat-btn']}
                                                onClick={() => handlerCopiesApplication(el.id)}
                                            >Дублировать</button>
                                            <Icon
                                                image={copyImage}
                                                width={16}
                                                height={16}
                                            />
                                        </div>
                                    </div>
                                    : null
                            }
                            <div className={style['card-app__data-card-block']}>
                                <div className={style['card-app__data-card-user']}>
                                    {el.user}
                                </div>
                                <div className={style['card-app__data-card-auto']}>
                                    {el.brand} | {el.model} | {el.generation}
                                </div>
                                <div className={style['card-app__data-card-description']}>
                                    {el.text}
                                </div>

                                {

                                    el?.images &&
                                    <ul className={style['card-app__data-card-container-image']}>
                                        {
                                            el.images.map(image => {
                                                return (
                                                    <li key={image.id} className={style['card-app__data-card-item-image']}>
                                                        <Icon
                                                            image={image.url}
                                                            width={63}
                                                            height={63}
                                                            onClick={() => el.images.length ? openModalImage(el.images) : null}
                                                        />
                                                    </li>
                                                )
                                            })
                                        }
                                    </ul>
                                }
                            </div>

                        </div>
                    )
                })
            }
        </div>
    )
}

export default CardApplicationAutoDuplicate;

