import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { activeButtonBootomForConfirm, changeStateBackButton, getOptions, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, } from "../../../../helpers";
import Button from "../../../../Views/Button/Button";
import Offset from "../../../../Views/Offset";
import Spinner from "../../../../Views/Spinner";
import Message from "../../../../Views/Messages/Message/Message";
import Pagination from "../../../../Views/Pagination/Pagination";
import { SafeAreaView, StyleSheet, Dimensions, ScrollView } from 'react-native-web';
import Label from "../../../../Views/Label/Label";
import NativeSelect from "../../../../Views/NativeSelect/NativeSelect";
import CardApplicationAutoDuplicate from "../../../../Views/CardApplicationAutoDuplicate/CardApplicationAutoDuplicate";


const CreateSearchParserList = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingGo, setIsLoadingGo] = useState(false);
  const { dispatch, tg, schemaColorList } = useStoreon("tg", 'schemaColorList');
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [listAplication, setListAplication] = useState({
    count: 0,
    results: [],
  });
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {

    dispatch("setTextHeader", state?.name_button ?? "КАТАЛОГ РАЗБОРА");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate("/button_parsing", {
        state: {
          name_button: state?.old_name_button ?? "АВТО В РАЗБОРЕ",
        },
      })
    );

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_cars/",
      page_id: state?.action_page_id ?? 3,
      type: "brand",
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        if (res?.result.length) {
          const copyData = res?.result?.slice();
          setDataRequestBrand((c) => [...c, ...getOptions(copyData)]);
        }
      },
    };
    dispatch("getContext", params);

  }, []);

  const handlerSubmitSearch = (data, { setFieldValue }) => {
    setIsLoadingGo(true);
    delete data["type"];
    // delete data["brand_id"];
    delete data["page_id"];
    delete data["text"];
    setListAplication(c => ({
      ...c,
      count: 0,
      results: [],
    }));
    setMessageInForm({
      textMessage: "",
      colorMessage: "",
    });
    const params = {
      ...data,
      confirmShow: true,
      url: "/api_search_parsings/",
      setFieldValue: () => { },
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        const copyData = res?.result?.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          brand_id: data.brand_id,
          model_id: data.model_id,
          generation_id: data.generation_id,
          results: [...copyData],
        }));

        setMessageInForm((c) => ({
          ...c,
          textMessage: '',
          colorMessage: "#555050",
        }));
      },
      redirectTo: () => { },
    };
    dispatch("setDataCreateRequest", params);
  };

  const changePaginationPage = (data) => {
    const { model_id, generation_id, page } = data;
    const paramsList = {
      url: "/api_search_parsings/",
      page: page ?? 1,
      page_size: 30,
      model_id,
      generation_id,
      setFieldValue: () => { },
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        const copyData = res?.result?.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          results: [...copyData],
        }));
      },
      redirectTo: (res) => { },
    };
    dispatch("setDataCreateRequest", paramsList);
  };

  const handlerChangeData = (changeData) => {
    let params = {
      url: "/api_get_cars/",
      page_id: state?.action_page_id,
      type: changeData.type,
      dataRequst: changeData.handlerChangeDataRequest,
    };
    if (changeData.brand_id) {
      params = {
        ...params,
        brand_id: changeData.brand_id,
      };
    }
    if (changeData.model_id) {
      params = {
        ...params,
        model_id: changeData.model_id,
      };
    }
    dispatch("getContext", params);
    return false;
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitSearch(values, setFieldValue);
  };

  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })

  const margin = Dimensions.get('window').width - 320;

  return (
    <SafeAreaView style={[styles.container, {
      width: '100%',
      // marginBottom: 100,
      marginTop: 8,
      paddingLeft: margin / 2,
      paddingRight: margin / 2,
    }]}>
      {dataRequstBrand.length ? (
        <SafeAreaView style={[styles.container, {
          width: '100%',
          // marginBottom: 100,

        }]}>
          <Message
            noicon
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          <Formik
            initialValues={{
              optionsBrand: dataRequstBrand,
              optionsModel: [],
              optionsGeneration: [],
              type: state?.type,
              brand_id: "",
              model_id: "",
              generation_id: "",
              text: "",
              uploadFiles: null,
              page: 1,
              page_size: 30,
            }}
            onSubmit={handlerSubmitSearch}
          >
            {({ values, errors, setFieldValue }) => {
              const handlerChangeDataRequest = (data) => {
                let options = getOptions(data.results);
                if (data.type === "model")
                  setFieldValue("optionsModel", options);
                if (data.type === "generation")
                  setFieldValue("optionsGeneration", options);
              };

              return (
                <React.Fragment>
                  <Form
                    style={{
                      width: "100%",
                      position: "relative",
                    }}
                  >
                    <Label>Марка авто</Label>
                    <NativeSelect
                      data={values.optionsBrand}
                      enabled={true}
                      placeholder={'Выберите марку авто'}
                      selectedValue={values.brand_id}
                      onChange={(value) => {
                        setFieldValue("brand_id", value);
                        setFieldValue("generation_id", "");
                        setFieldValue("model_id", "");
                        setFieldValue("optionsGeneration", []);
                        setDataButtonBottonTrigger(false, tg);
                        handlerChangeData({
                          type: "model", brand_id: value, handlerChangeDataRequest: (res) => {
                            const copyData = res.result.slice();
                            handlerChangeDataRequest({
                              results: copyData,
                              type: res.type,
                            });
                          },
                        });
                      }}
                    />

                    <Label>Модель авто</Label>
                    <NativeSelect
                      data={values.optionsModel}
                      enabled={!!values.brand_id}
                      placeholder={'Выберите модель авто'}
                      selectedValue={values.model_id}
                      onChange={(value) => {
                        setFieldValue("model_id", value);
                        setFieldValue("generation_id", "");
                        setDataButtonBottonTrigger(false, tg);
                        handlerChangeData({
                          type: "generation", model_id: value, handlerChangeDataRequest: (res) => {
                            const copyData = res.result.slice();
                            handlerChangeDataRequest({
                              results: copyData,
                              type: res.type,
                            });
                          },
                        });
                      }}
                    />
                    <Label>Год выпуска авто</Label>
                    <NativeSelect
                      data={values.optionsGeneration}
                      enabled={!!values.model_id}
                      placeholder={'Выберите год выпуска авто'}
                      selectedValue={values.generation_id}
                      onChange={(value) => {
                        setFieldValue("generation_id", value);
                        activeButtonBootomForConfirm(tg, { ...values, generation_id: value }, "search", "посмотреть");
                      }}
                    />

                    <button
                      className="btn-click"
                      onClick={() => handlerVirtualClick(values, setFieldValue)}
                    ></button>
                    {!!!tg.initDataUnsafe?.query_id ? (
                      <Offset mt={10}>
                        <Button
                          disabled={
                            !(
                              values.generation_id &&
                              values.brand_id &&
                              values.model_id
                            )
                          }
                          type={"submit"}
                          addClass={"form-submit__btn"}
                          active={
                            values.generation_id &&
                            values.brand_id &&
                            values.model_id
                          }
                        >
                          <span>
                            <span>посмотреть</span>
                          </span>
                        </Button>
                      </Offset>
                    ) : null}
                  </Form>
                </React.Fragment>
              );
            }}
          </Formik>
        </SafeAreaView>
      ) : (
        <Spinner />
      )}

      {listAplication.results.length && !isLoading ? (
        <ScrollView
          style={[styles.container, {
            backgroundColor: schemaColorList.colorFoneBackground,
            width: '100%',
            // marginBottom: 100,           
            marginTop: margin / 4,
            paddingLeft: margin / 2,
            paddingRight: margin / 2,
          }]}
        >
          <Message
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          <CardApplicationAutoDuplicate
            listAplication={listAplication}
            noclick
          />
          {/* <div
              style={{
                color: `var(--text-color)`,
              }}
            >
              Результат поиска: {listAplication?.results.length}
            </div> */}

          {/* <CardApplication noAction listAplication={listAplication} /> */}

          {listAplication.count > 30 ? (
            <Pagination
              data={listAplication}
              allCount={listAplication.count}
              count={30}
              handlerChangePaginations={changePaginationPage}
              currentPage={Number(listAplication.current_page)}
              location={"center"}
            />
          ) : null}
        </ScrollView>
      ) : isLoadingGo ? (
        <Spinner text={`Результат поиска: ${listAplication?.results.length}`} />
      ) : null}
    </SafeAreaView>
  );
};
export default CreateSearchParserList;
