import { Dimensions, FlatList, SafeAreaView, StyleSheet, View } from "react-native-web"
import ItemMainMenu from "./Detail/ItemMainMenu";
import { useStoreon } from "storeon/react";

const width = Dimensions.get('window').width - 320;
const NativeListMenu = ({
    list,    
    handlerChangeScreen,    
}) => {
    const { schemaColorList } = useStoreon('schemaColorList');
    return (
        <SafeAreaView style={[styles.container]}>
            <View style={[styles.body, { marginBottom: 0 }]} >

                <FlatList
                    data={list.length && list.filter(el => el.id !== 999)}
                    numColumns={2}
                    columnWrapperStyle={{
                        marginBottom: 10,
                        width: '100%',
                        maxWidth: 320,
                        // marginLeft: width + 10,
                        // marginRight: width ,
                    }}
                    renderItem={({ item }) => <ItemMainMenu item={item} colorText={schemaColorList.colorText} colorFoneBackgroundElement={schemaColorList.colorFoneBackgroundElement} handlerChangeScreen={handlerChangeScreen} />}
                    keyExtractor={item => item.id}
                />

            </View>

        </SafeAreaView>
    )
}

export default NativeListMenu;

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 8,
    },
    body: {

    },
    title: {
        fontSize: 32,
    },
});
