import React, { useState } from 'react';
import classNames from 'classnames';

import style from './styles/toggle.module.scss';
import Icon from '../Icon/Icon';
import { useStoreon } from 'storeon/react';

const Toggle = ({ 
  active,
  leftToggle,
  rightToggle,
  leftToggleOnClick,
  rightToggleOnClick,
  iconLeft,
  iconRight,
 }) => {
  const [ activeToggle, setActiveToggle ] = useState(false);
  const { schemaColorList } = useStoreon('schemaColorList');

  const handlerActiveToggleLeft = classNames({
    [style['toggle__item']]:true,
    [style['toggle__item--active']]: !activeToggle
  })
  const handlerActiveToggleRight = classNames({
    [style['toggle__item']]:true,
    [style['toggle__item--active']]: activeToggle
  })
  return (
    <div
      className={style['toggle__container']}
    >
      <div
        className={handlerActiveToggleLeft}
        onClick={()=>{
          setActiveToggle(false)
          leftToggleOnClick()
        }}
        >     
      {leftToggle}
      {
        iconLeft && 
        <Icon
          image={iconLeft}
          width={16}
          height={16} 
          ml={10}
          invert={schemaColorList.invertTheme? 0 : 1 }
          />
        }
      </div>
      <div
        className={handlerActiveToggleRight}
        onClick={()=>{
          setActiveToggle(true)
          rightToggleOnClick()
        }}
      >      
      {rightToggle}
      {
        iconRight && 
        <Icon
          invert={schemaColorList.invertTheme? 0 : 1}
          image={iconRight}
          width={16}
          height={16} 
          ml={10}
        />
      }
      </div>
    
    </div>
  );
};

export default Toggle;
