import { Form, Formik } from "formik";
import React, { Fragment, useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import {
  activeButtonBootomForConfirm,
  changeStateBackButton,
  getOptionsPayment,
  getOptionsPaymentPeriod,
  handlerWarningInfoMessageResponse,
  setDataButtonBottonTrigger,
  smoothTop,
} from "../../../helpers";
import Button from "../../../Views/Button/Button";
import FormPaymentAccess from "../../../Views/FormPaymentAccess/FormPaymentAccess";
import Message from "../../../Views/Messages/Message/Message";
import MessagePayment from "../../../Views/Messages/MessagePayment/MessagePayment";
import Offset from "../../../Views/Offset";
import SelectDesktop from "../../../Views/Select/SelectDesktop";
import SelectPayment from "../../../Views/Select/SelectPayment";
import Spinner from "../../../Views/Spinner";
import BlockWrapper from "../../../Views/BlockModules/BlockWrapper";
import Autopayment from "../../../Views/Autopayment/Autopayment";
import { testId } from "../../../helpers/const";
import { Dimensions, SafeAreaView, StyleSheet, View } from "react-native-web";
import Label from "../../../Views/Label/Label";

const Payment = () => {
  const { state } = useLocation();
  const { tg, dispatch, context, schemaColorList } = useStoreon("tg", 'context', 'schemaColorList');
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [autoPayment, setAutoPayment] = useState(context?.children?.buttons[0]?.children?.auto_payment ?? state?.auto_payment);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });
  const [messageInFormWarning, setMessageInFormWarning] = useState({
    textMessage: "",
    colorMessage: "",
  });
  const [messageInFormDop, setMessageInFormDop] = useState({
    textMessage: "",
    colorMessage: "",
  });
  const idUser = tg?.initDataUnsafe?.user?.id ?? testId;

  useEffect(() => {

    const params = {
      url: "/api_get_context_profile/",
      page_id: state?.action_page_id ?? 31,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
      },
    };
    dispatch("getContext", params);

  }, []);

  useEffect(() => {
    context?.children?.buttons[0]?.children?.auto_payment && setAutoPayment(context?.children?.buttons[0]?.children?.auto_payment);
  }, [context?.children?.buttons[0]?.children?.auto_payment]);

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? "подписка");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() => navigate("/button_my_profile"), {
      state: {
        name_button: state?.old_name_button ?? 'подписка'
      }
    });

  }, []);

  useEffect(() => {

    context?.children?.buttons[0]?.children?.message && setMessageInForm((c) => ({
      ...c,
      textMessage: context?.children?.buttons[0]?.children?.message,
      colorMessage: context?.children?.buttons[0]?.children?.colorMessage ?? "#555050",
    }));

  }, [context?.children?.buttons[0]?.children?.message]);

  useEffect(() => {

    context?.children?.buttons[0]?.children?.add_message && setMessageInFormDop((c) => ({
      ...c,
      textMessage: context?.children?.buttons[0]?.children?.add_message,
      colorMessage: context?.children?.buttons[0]?.children?.colorMessage ?? "#555050",
    }));

  }, [context?.children?.buttons[0]?.children?.add_message]);

  const handlerSubmitChooseAggrigate = (data, { setFieldValue }) => {
    delete data["optionsAccess"];
    delete data["optionsBank"];
    delete data["optionsPeriod"];
    delete data["access_text"];
    delete data["bank_text"];
    delete data["period_text"];
    delete data["auto_payment_status"];

    const params = {
      ...data,
      url: "/api_get_pay_url/",
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        setFieldValue("period_text", "");
        setFieldValue("period", "");
        setFieldValue("bank", "");
        setFieldValue("bank_text", "");
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInFormWarning);
        if (isWarning) return;

        tg.openLink(`${res.result.url}`);
        navigate("/button_my_profile", {
          state: {
            name_button: state?.old_name_button,
          },
        });
      },
      redirectTo: res => { },
    };
    dispatch("setDataCreateRequest", params);
    return false;
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitChooseAggrigate(values, setFieldValue);
  };

  const handlerAccessPayment = (access) => {
    // функция оплаты предыдущего доступа доступа, осталась на стадии разработки ещё не насыщаю данными с бэка
    let params = {
      url: "/api_get_pay_url/",
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        tg.openLink(`${res.result.url}`);
      },
      redirectTo: (res) => { },
    };

    console.log({ params });
    //dispatch('setDataCreateRequest', params);
    return false;
  };

  const hendlerSubscribe = (value) => {
    const params = {
      url: "/api_cancel_user_auto_payment/",
      setIsLoading: () => { },
      dataRequst: (res) => {
        setAutoPayment(false);
      },
    };
    return dispatch("getContextCancelAutoPayment", params);
  };




  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })
  const margin = Dimensions.get('window').width - 320;

  return (
    <SafeAreaView
      style={[styles.container, {
        // backgroundColor: schemaColorList.colorFoneBackgroundElement,
        width: '100%',
        marginTop: 8,
      }]}
    >
      {
        !isLoading && Object.keys(context).length ? (
          <View>

            <View
              style={[styles.container, {
                // backgroundColor: schemaColorList.colorFoneBackgroundElement,
                width: '100%',
                marginTop: margin / 4,
                paddingLeft: margin / 3,
                paddingRight: margin / 3,
              }]}
            >
              <MessagePayment
                noicon
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
                handlerAccessPayment={handlerAccessPayment}
                tg={tg}
                hendlerSubscribe={hendlerSubscribe}
              />
              <Message
                noicon
                message={messageInFormWarning.textMessage ? messageInFormWarning.textMessage : "Вы можете выбрать из двух видов доступа:"}
                colorMessage={schemaColorList.colorText}
              />
            </View>
            {/* 
            <Message
              message={statusAutopayment()}
              colorMessage={messageInFormDop.colorMessage}
            /> */}
            <Formik
              initialValues={{
                optionsAccess: getOptionsPayment(context?.children?.buttons[0]?.children?.drop_down_list_access),
                optionsBank: getOptionsPayment(context?.children?.buttons[0]?.children?.drop_down_list_bank),
                optionsPeriod: getOptionsPaymentPeriod(
                  context?.children?.buttons[0]?.children.drop_down_list_period,
                  "full"
                ),
                access_text: context?.children?.buttons[0]?.children?.drop_down_list_access[0]?.value,
                access: context?.children?.buttons[0]?.children?.drop_down_list_access[0]?.key_value,
                auto_payment_status: autoPayment,
                auto_payment: true,
                bank_text: "",
                bank: "",
                period_text: "",
                period: "",
              }}
              onSubmit={handlerSubmitChooseAggrigate}
            >
              {({ values, setFieldValue }) => {
                return (
                  <React.Fragment>
                    <Form
                      style={{
                        width: "100%",
                        position: "relative",
                      }}
                    >
                      <BlockWrapper>
                        {messageInFormDop.textMessage ? (
                          <FormPaymentAccess
                            setDataButtonBottonTrigger={setDataButtonBottonTrigger}
                            typeAccess={values.optionsAccess}
                            setFieldValue={setFieldValue}
                            values={values}
                            messageInFormDop={messageInFormDop}
                            state={context?.children?.buttons[0]?.children}
                            tg={tg}
                          />
                        ) : null}

                        <SafeAreaView style={[styles.container, {
                          // backgroundColor: schemaColorList.colorFoneBackgroundElement,
                          width: '100%',
                          marginTop: 8,
                          paddingLeft: margin / 2,
                          paddingRight: margin / 2,
                        }]}>
                          {/* <Offset mb={90}> */}

                          <Label>Срок подписки</Label>
                          <SelectPayment
                            options={values.optionsPeriod}
                            value={values.period_text}
                            placeholder={"сделайте Ваш выбор"}
                            addClass={"select__dropdown-list-payment"}
                            name={"period_text"}
                            id={3}
                            style={{ zIndex: 997 }}
                            onClick={(e) => {
                              const value = e.target.getAttribute("value");
                              const key_value = e.target.getAttribute("key_value");
                              setFieldValue("period_text", value);
                              setFieldValue("period", key_value);
                              setFieldValue("bank", "");
                              setFieldValue("bank_text", "");
                              setDataButtonBottonTrigger(false, tg);
                            }}
                          />

                          <Label>Способ оплаты</Label>
                          <SelectDesktop
                            options={values.optionsBank}
                            disable={!values.period_text}
                            value={values.bank_text}
                            placeholder={"сделайте Ваш выбор"}
                            addClass={"select__dropdown-list-car"}
                            name={"bank_text"}
                            id={2}
                            style={{ zIndex: 996 }}
                            onClick={(e) => {
                              const value = e.target.getAttribute("value");
                              const key_value = e.target.getAttribute("key_value");
                              setFieldValue("bank_text", value);
                              setFieldValue("bank", key_value);
                              activeButtonBootomForConfirm(
                                tg,
                                { ...values, bank: key_value },
                                "payment",
                                "перейти к оплате"
                              );
                            }}
                          />


                          <Message
                            noicon
                            message={<Autopayment values={values} setFieldValue={setFieldValue} hendlerSubscribe={hendlerSubscribe} />}
                          />
                          {/* </Offset> */}
                        </SafeAreaView>

                        {/* <Offset mb={30} /> */}

                        <Offset mb={90}>
                          <button
                            className="btn-click"
                            onClick={() =>
                              handlerVirtualClick(values, setFieldValue)
                            }
                          ></button>
                          {!!!tg.initDataUnsafe?.query_id ? (
                            <Button
                              disabled={
                                !(values.access && values.bank && values.period)
                              }
                              type={"submit"}
                              addClass={"form-submit__btn"}
                              active={values.access && values.bank && values.period}
                            >
                              <span>
                                <span>оплатить</span>
                              </span>
                            </Button>
                          ) : null}
                        </Offset>

                      </BlockWrapper>
                    </Form>
                  </React.Fragment>
                );
              }}
            </Formik>
          </View>
        ) : (
          <Spinner />
        )
      }
    </SafeAreaView>
  );
};

export default Payment;
