import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import CardApplicationTopReview from '../../../../Views/CardApplicationTopReview/CardApplicationTopReview';
import Message from '../../../../Views/Messages/Message/Message';
import Pagination from '../../../../Views/Pagination/Pagination';
import { Scrollable, changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import {
    SafeAreaView,
    View,
    VirtualizedList,
    StyleSheet,
    Text,
    StatusBar,
} from 'react-native';

const TopReview = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {

        dispatch('setTextHeader', state?.name_button ?? 'ТОП-100');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);

        tg.BackButton.onClick(() => navigate('/button_rating', {
            state: {
                name_button: state?.old_name_button ?? 'РЕЙТИНГ И ОТЗЫВЫ'
            }
        }));

    }, []);

    useEffect(() => {

        const paramsList = {
            url: '/api_top_rating/',
            page: 1,
            page_size: 30,
            dataRequst: (res) => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                setListAplication(c => ({ ...c, ...res.result }));
            },
        };
        setTimeout(() => {
            dispatch('getListApplications', paramsList)
        }, 100);

    }, []);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_top_rating/',
            page: page ?? 1,
            page_size: 30,
            setFieldValue: () => { },
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
            redirectTo: (res) => { }
        };
        dispatch('getListApplications', paramsList)
    }

    const handlerChangeScreen = (url) => {
        const callback = (status, link) => {
            if (status) {
                return window.open(link);
            }
            return false;
        }
        console.log({ tg })
        if (tg.initDataUnsafe?.query_id)
            tg?.showConfirm(`Перейти к продавцу?`, e => callback(e, url));
    }

    return (
        <React.Fragment>
            <Message
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
            />
            {
                listAplication.results.length ?
                    <React.Fragment>
                        <CardApplicationTopReview
                            listAplication={listAplication}
                            handlerChangeScreen={handlerChangeScreen}
                            colorFoneBackgroundElement={schemaColorList.colorFoneBackgroundElement}
                            colorText={schemaColorList.colorText}
                        />
                        {
                            listAplication.count > 30 ?
                                <Pagination

                                    data={listAplication.results}
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={changePaginationPage}
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                /> : null
                        }
                        <Offset mb={90} />
                    </React.Fragment>
                    : <Spinner
                        text={'Список отзывов пуст'}
                    />
            }
        </React.Fragment>
    )
}
export default TopReview;

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    item: {
        backgroundColor: '#f9c2ff',
        height: 150,
        justifyContent: 'center',
        marginVertical: 8,
        marginHorizontal: 16,
        padding: 20,
    },
    title: {
        fontSize: 32,
    },
});