import classNames from 'classnames';
import React from 'react';

import styleMain from './styles/icon.module.scss';

const Icon = ({
    value,
    style,
    image,
    width = 20,
    height = 20,
    mr = 0,
    ml = 0,
    mt=0,
    mb=0,
    filter,
    className,
    onClick,
    invert,
    opacity,
    brightness,
    zIndex,
    children,
}) => {
const styleImage = classNames({
    [styleMain['icon__image']]:true,
    [className]:!!className,
    
})
const dataFilter = () => {
    let value
    if(invert !== undefined && invert !== null ){
        console.log({invert : invert})
        value = `invert(${invert})` 
    }
    // if(brightness !== undefined || brightness !== null ){
    //     value = `invert(${invert})` 
    // }

    //  `${
    //     invert && 
    //         && brightness? 
    //             brightness
    //             : (invert && brightness)? 
    //                 `${invert, brightness}`    
    //                 : 
    //                 filter}`
    return value;
}
return(
<React.Fragment>
    <div
        value={value}
        className={styleImage}
        onClick={onClick}
        style={{
            backgroundImage: `url(${image})`,
            width: `${width}px`,
            minWidth: `${width}px`,
            height: `${height}px`,
            marginRight: `${mr}px`,
            marginLeft: `${ml}px`,
            marginTop: `${mt}px`,
            marginBottom: `${mb}px`,
            zIndex: zIndex,
            filter: dataFilter(),
            opacity: `${opacity}`,
            ...style
        }}
    >{children}</div>
</React.Fragment>
)
}
export default Icon;