import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import * as Yup from 'yup';
import Phone from 'react-phone-number-input';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import {
  activeButtonBootomForConfirm,
  changeStateBackButton,
  getOptions,
  handlerWarningInfoMessageResponse,
  setDataButtonBottonTrigger,
} from '../../../../helpers';
import Button from '../../../../Views/Button/Button';
import Message from '../../../../Views/Messages/Message/Message';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import TextArea from '../../../../Views/TextArea/TextArea';
import FormUploadImage from '../../../../Views/UploadImage/FormUploadImage';
import PhoneContainer from '../../../../Views/PhoneContainer/PhoneContainer';
import { statusAgreeShowPhone } from '../../../../helpers/const';
import { Dimensions, SafeAreaView, StyleSheet } from 'react-native-web';
import Label from '../../../../Views/Label/Label';
import NativeSelect from '../../../../Views/NativeSelect/NativeSelect';

const AggregateRequestSpare = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, schemaColorList, tg, userStatus } = useStoreon('tg', 'userStatus', 'schemaColorList');
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: '',
    colorMessage: '',
  });
  const phoneRegExp = /^([+]?[\s0-9]+)?(\d{3}|[(]?[0-9]+[)])?([-]?[\s]?[0-9])+$/;

  useEffect(() => {
    dispatch('setTextHeader', state?.name_button ?? 'ЗАПРОС НА ЗАПЧАСТЬ');
    dispatch('setActiveHeaderText', true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate('/button_make_request', {
        state: {
          name_button: state?.old_name_button ?? '',
        },
      })
    );
  }, []);

  useEffect(() => {
    const params = {
      url: '/api_get_cars/',
      page_id: state?.action_page_id ?? 2,
      type: 'brand',
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        if (res?.result?.length) {
          const copyData = res?.result?.slice();
          setDataRequestBrand((c) => [...c, ...getOptions(copyData)]);
        }

      },
    };
    dispatch('getContext', params);

    if (!!!userStatus) {
      dispatch('requestUserStatus', {
        url: '/api_get_page/',
        page_id: state?.action_page_id ?? 2,
      });
    }
  }, []);

  const handlerSubmitChooseAggrigate = (data, { setFieldValue }) => {
    const params = {
      ...data,
      url: '/api_create_request/',
      setFieldValue: setFieldValue,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
      },
      redirectTo: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        dispatch('openModal', {
          // content = 'text'
          // show = boolean
          // contentBtn = 'надпись кнопки закрытия'
          // error = boolean image
          show: res?.result?.info?.status,
          content: res?.result?.info?.message,
          contentBtn: 'ok',
          error: !res?.result?.info?.status,
          path: '/button_make_request',
          state: {
            ...state,
            // textMessage: res?.result?.info.message,
            // colorMessage: res?.result?.info.status,
            name_button: state?.old_name_button ?? 'СОЗДАТЬ ЗАПРОС',
          },
        })
        // navigate('/button_make_request', {
        //   state: {
        //     ...state,
        //     textMessage: res?.result?.info.message,
        //     colorMessage: res?.result?.info.status ? '#555050' : '#ff0000',
        //   },
        // });
      },
    };
    dispatch('setDataCreateRequest', params);
    return false;
  };

  const handlerChangeData = (changeData) => {
    let params = {
      url: '/api_get_cars/',
      type: changeData.type,
      dataRequst: changeData.handlerChangeDataRequest,
    };
    if (changeData.brand_id) {
      params = {
        ...params,
        brand_id: changeData.brand_id,
      };
    }
    if (changeData.model_id) {
      params = {
        ...params,
        model_id: changeData.model_id,
      };
    }

    dispatch('getContext', params);
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitChooseAggrigate(values, setFieldValue);
  };

  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })
  const margin = Dimensions.get('window').width - 320;
  return (
    <SafeAreaView style={[styles.container, {
      backgroundColor: schemaColorList.colorFoneBackgroundElement,
      width: '100%',
      marginTop: margin / 4,
    }]}>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      {dataRequstBrand.length ? (
        <Formik
          validationSchema={() => {
            return Yup.object().shape({
              phone_number: Yup.string().matches(
                phoneRegExp,
                'Не верный формат номера'
              ),
            });
          }}
          initialValues={{
            optionsBrand: dataRequstBrand,
            optionsModel: [],
            optionsGeneration: [],
            type: state?.type ?? 'spare',
            brand_id: '',
            model_id: '',
            generation_id: '',
            text: '',
            uploadFiles: null,
            phone_number: '',
            isPhone: !(+userStatus === statusAgreeShowPhone),
          }}
          onSubmit={handlerSubmitChooseAggrigate}
        >
          {({ values, setFieldValue }) => {
            const handlerChangeDataRequest = (data) => {
              let options = getOptions(data.results);
              if (data.type === 'model') setFieldValue('optionsModel', options);
              if (data.type === 'generation')
                setFieldValue('optionsGeneration', options);
            };
            return (
              <Form>
                <SafeAreaView style={[styles.container, {
                  paddingLeft: margin / 2,
                  paddingRight: margin / 2,
                }]}>
                  <Label>Марка авто</Label>
                  <NativeSelect
                    data={values.optionsBrand}
                    enabled={true}
                    selectedValue={values.brand_id}
                    placeholder={'Выберите марку авто'}
                    onChange={(value) => {
                      console.log({ value })
                      setFieldValue('brand_id', value);
                      setFieldValue('generation_id', '');
                      setFieldValue('model_id', '');
                      setDataButtonBottonTrigger(false, tg);
                      handlerChangeData({
                        type: 'model',
                        brand_id: value,
                        handlerChangeDataRequest: (res) => {
                          const copyData = res.result.slice();
                          handlerChangeDataRequest({
                            results: copyData,
                            type: res.type,
                          });
                        },
                      });
                    }}
                  />
                  <Offset mt={16} />
                  <Label>Модель авто</Label>
                  <NativeSelect
                    data={values.optionsModel}
                    enabled={!!values.brand_id}
                    placeholder={'Выберите модель авто'}
                    selectedValue={values.model_id}
                    onChange={(value) => {
                      console.log({ value })
                      setFieldValue('model_id', value);
                      setFieldValue('generation_id', '');
                      setDataButtonBottonTrigger(false, tg);
                      handlerChangeData({
                        type: 'generation',
                        model_id: value,
                        handlerChangeDataRequest: (res) => {
                          const copyData = res.result.slice();
                          handlerChangeDataRequest({
                            results: copyData,
                            type: res.type,
                          });
                        },
                      });
                    }}
                  />
                  <Offset mt={16} />
                  <Label>Год выпуска авто</Label>
                  <NativeSelect
                    data={values.optionsGeneration}
                    enabled={!!values.model_id}
                    selectedValue={values.generation_id}
                    placeholder={'Выберите год выпуска авто'}
                    onChange={(value) => {
                      setFieldValue('generation_id', value);
                      if (!value) {
                        return setDataButtonBottonTrigger(false, tg);
                      }
                      activeButtonBootomForConfirm(tg, { ...values, generation_id: value, isPhone: +userStatus === statusAgreeShowPhone ? values.phone_number ? true : false : true }, 'unit-spare', 'Опубликовать');
                    }}
                  />
                  <Offset mt={26} />
                  <TextArea
                    className={'textarea-application'}
                    value={values.text}
                    name={'text'}
                    placeholder={'Введите данные для запроса'}
                    height={110}
                    onChange={(e) => {
                      const value = e.target.value;
                      setFieldValue('text', value);
                      if (!value) {
                        return setDataButtonBottonTrigger(false, tg);
                      }
                      activeButtonBootomForConfirm(tg, { ...values, text: value, isPhone: +userStatus === statusAgreeShowPhone ? values.phone_number ? true : false : true, }, 'unit-spare', 'Опубликовать');
                    }}
                  />
                  {+userStatus === statusAgreeShowPhone ? (
                    <Offset mt={16}>
                      <Label>Телефон</Label>
                      <PhoneContainer>
                        <Phone
                          placeholder={'+7 (000) 000-00-00'}
                          value={values.phone_number}
                          name={'phone_number'}
                          className={'form-input-number-phone'}
                          style={{
                            height: 50,
                            width: '100%',
                            borderRadius: 8,
                            border: `1px solid ${schemaColorList.borderColor}`,
                            borderWidth: 1,
                            // backgroundColor: schemaColorList.colorFoneBackground,
                            color: schemaColorList.placeholder
                          }}
                          onChange={(phone) => {
                            if (!phone || phone.length < 8) {
                              setFieldValue('phone_number', phone);
                              setFieldValue('isPhone', false);
                              return setDataButtonBottonTrigger(false, tg);
                            }
                            setFieldValue('phone_number', phone);
                            setFieldValue('isPhone', true);
                            activeButtonBootomForConfirm(
                              tg,
                              {
                                ...values,
                                phone_number: phone,
                                isPhone: true,
                              },
                              'unit-spare',
                              'Опубликовать'
                            );
                          }}
                        />
                        {/* {!values?.phone_number && (
                        <ErrorMessage>
                          {' '}
                          Поле не может быть пустым{' '}
                        </ErrorMessage>
                      )} */}
                      </PhoneContainer>
                    </Offset>
                  ) : null}

                  <Label>Добавить изображение</Label>

                  <FormUploadImage
                    multiple
                    values={values}
                    setFieldValue={setFieldValue}
                  // label={'прикрепить изображения'}
                  />

                  <button
                    className='btn-click'
                    onClick={() => handlerVirtualClick(values, setFieldValue)}
                  ></button>
                  {!!!tg.initDataUnsafe?.query_id ? (
                    <Button
                      disabled={
                        !(
                          values.text &&
                          values.generation_id &&
                          values.brand_id &&
                          values.model_id &&
                          (+userStatus === statusAgreeShowPhone
                            ? !!values?.phone_number
                            : true)
                        )
                      }
                      type={'submit'}
                      addClass={'form-submit__btn'}
                      active={
                        values.text &&
                        values.generation_id &&
                        values.brand_id &&
                        values.model_id &&
                        (+userStatus === statusAgreeShowPhone
                          ? !!values?.phone_number
                          : true)
                      }
                    >
                      <span>
                        <span>создать</span>
                      </span>
                    </Button>
                  ) : null}
                </SafeAreaView>
              </Form>
            );
          }}
        </Formik>
      ) : (
        <Spinner />
      )}
    </SafeAreaView>
  );
};
export default AggregateRequestSpare;
