import React from "react";
import { info } from "../../../images";
import Icon from "../../Icon/Icon";
import MessageBlockPayment from '../MessageBlockPayment/MessageBlockPayment';
import FormProfileData from "./FormProfileData";

const MessagePayment = ({
    message,
    noicon,
}) => {


    return (
        <React.Fragment>
            {
                            message?
                                <MessageBlockPayment>
                                    {
                                        !noicon? 
                                            <Icon image={info} width={15} height={15} mr={5} /> 
                                            : null
                                    }

                                    <FormProfileData
                                        message={message}
                                    />
                                </MessageBlockPayment>
                                : null
                                
                            }
        </React.Fragment>
    )
}

export default MessagePayment;