import { SafeAreaView, Picker, StyleSheet, Text, View, Image } from "react-native-web";
import { useState } from "react";
import { home } from '../../images/_icons/index'
import { useStoreon } from "storeon/react";
const NativeSelect = ({
    data = [],
    enabled = true,
    onChange,
    selectedValue,
    placeholder,
}) => {
    const countries = ["Egypt", "Canada", "Australia", "Ireland"]
    const {schemaColorList} = useStoreon('schemaColorList')
    // enabled = props.enabled,
    // onValueChange = props.onValueChange,
    // selectedValue = props.selectedValue,
    // style = props.style,
    // testID = props.testID,
    // itemStyle = props.itemStyle,
    // mode = props.mode,
    // prompt = props.prompt,
    // other = (0, _objectWithoutPropertiesLoose2.default)(props, _excluded);
    let newData = []
    newData = [{
        title: placeholder ?? 'сделайте выбор',
        value: 0
    }, ...data]
    return (
        <SafeAreaView style={styles.container}>
            <Picker
                onClick ={e=>{
                    document.documentElement.style.setProperty("--bg-native-select-color",schemaColorList.colorFoneBackgroundElement);
                }}
                enabled={enabled}
                selectedValue={selectedValue ?? 'select'}
                style={{ 
                    height: 50, 
                    width: '100%',
                    borderRadius: 8,
                    border: `1px solid ${schemaColorList.borderColor}`,
                    borderWidth: 1,                    
                    // backgroundColor: schemaColorList.colorFoneBackground,
                    color: schemaColorList.placeholder
                }}
                onValueChange={(itemValue, itemIndex) => {
                    onChange(itemValue)
                }
                }
            >
                {
                    newData.map( (el, index)=>{
                        return(
                                <Picker.Item 
                                    key={`${el.value}-${index}`}
                                    style={{
                                        border: `1px solid ${schemaColorList.borderColor}`,
                                        borderWidth: 1,                    
                                        backgroundColor: 'transparent',
                                        color: schemaColorList.placeholder
                                    }}
                                    label={ el.title }
                                    value={ el.value } 
                                />
                        )
                    })
                }
                {/* <Picker.Item label="JavaScript" value="js" /> */}
            </Picker>
        </SafeAreaView>
    )
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        // marginHorizontal: 16,
        borderRadius: 10
    },
    item: {
        backgroundColor: '#f9c2ff',
        padding: 20,
        marginVertical: 8,
    },
    header: {
        fontSize: 32,
        backgroundColor: '#fff',
    },
    title: {
        fontSize: 24,
    },
});

export default NativeSelect;