import React, { useRef } from 'react';

import classNames from 'classnames';
import style from './styles/input.module.scss';
import Icon from '../Icon/Icon';
import { useStoreon } from 'storeon/react';

const Input = ({
  variant, 
  className, 
  value, 
  children, 
  label, 
  autofocus, 
  type = 'text', 
  placeholder, 
  onChange,
  name, 
  autocomplete = 'off',
  helpText,
  addClass,
  icon,
  ...props
}) => {

  const inputRef = useRef(null);
  const { schemaColorList } = useStoreon('schemaColorList');

  return (
    <>
      <label
        className={style['input__label']}
      >{label}</label>
      <div
        className={classNames({
          [style['input__wrapper']]: true,
          [style[className]]: !!className,
          [addClass]: !!addClass
        })}
      >
        {
          icon &&
          <Icon
            invert={schemaColorList.invertTheme? 0 : 1  }
            image={icon}
            mt={12}
            ml={10}
          />
        }
        <input
          autoComplete={autocomplete}
          type={type}
          autoFocus={autofocus}
          onFocus={ autofocus? e => e.currentTarget.select() : null }
          className={style['input__input']}
          value={value}
          placeholder={placeholder}
          onChange={onChange}
          name={name}
          ref={inputRef}
        />
        {children}
        {
          helpText?
            helpText
            : null        
        }
      </div>

    </>
  )
}

export default Input;