import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBotton, setDataButtonBottonTrigger, smoothTop } from "../../helpers";
import CardApplication from "../../Views/CardApplication";
import ListMainButton from "../../Views/ListMainButton/ListMainButton";
import Message from "../../Views/Messages/Message/Message";
import Spinner from "../../Views/Spinner";
import { Dimensions, SafeAreaView, StyleSheet } from 'react-native-web';
import NativeListMenu from "../../Views/NativeListMenu/NativeListMenu";


const CreateInSales = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const { dispatch, contextCreateInSales, tg, schemaColorList } = useStoreon("contextCreateInSales", "tg", 'schemaColorList');

  const { message, buttons } = contextCreateInSales;
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });
  const [listAplication, setListAplication] = useState({
    count: 0,
    results: [],
  });

  useEffect(() => {
    state?.textMessage
      ? setMessageInForm((c) => ({
        ...c,
        textMessage: state?.textMessage,
        colorMessage: state?.colorMessage ?? "#555050",
      }))
      : message?.name
        ? setMessageInForm((c) => ({
          ...c,
          textMessage: message.name,
          colorMessage: message?.colorMessage ?? "#555050",
        }))
        : console.log("fake");
  }, [message?.name, state?.textMessage]);

  useEffect(() => {

    dispatch("setTextHeader", state?.name_button ?? "Авто на продаже");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() => navigate("/button_my_advert", {
      state: {
        name_button: state?.old_name_button ?? "ОБЪЯВЛЕНИЯ",
      },
    }));

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_page/",
      page_id: state?.action_page_id ?? 8,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
      },
    };
    dispatch("getContextCreateInSales", params);

  }, []);

  useEffect(() => {

    const paramsList = {
      url: "/api_get_sales/",
      page: 1,
      page_size: 5,
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        const copyData = res.result.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          results: [...copyData],
        }));
      },
    };
    dispatch("getListApplications", paramsList);

  }, []);

  const handlerDeleteApplication = (id) => {
    const params = {
      url: "/api_delete_sale/",
      parsing_id: id,
      setIsLoading: setIsLoading,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        const copyData = res.result.results.slice();
        setListAplication((c) => ({
          ...c,
          ...res.result,
          results: [...copyData],
        }));
      },
    };
    dispatch("deleteApplicationId", params);
  };

  const handlerChangeApplication = (id) => {
    navigate("/button_sales/button_edit_sale_form", {
      state: {
        application: id,
      },
    });
  };

  const handlerInCatalogApplication = (id) => {
    navigate("/button_sales/button_edit_sale", {
      state: {
        application: id,
      },
    });
  };

  const handlerChangeScreen = (url) => {
    navigate(url)
  }

  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })
  const margin = Dimensions.get('window').width - 320;

  return (
    <SafeAreaView
      style={[styles.container, {
        // backgroundColor: schemaColorList.colorFoneBackgroundElement,
        width: '100%',
        marginTop: 8,
        paddingLeft: margin / 2,
        paddingRight: margin / 2,
      }]}
    >
      {"buttons" in contextCreateInSales ? (
        <React.Fragment>
          <Message
            noicon
            message={messageInForm.textMessage}
            colorMessage={messageInForm.colorMessage}
          />

          {/* <ListMainButton
            old_name_button={state?.name_button}
            localPath={"/button_sales/"}
            dispatch={dispatch}
            list={buttons}
          /> */}
          <NativeListMenu
            list={buttons}
            handlerChangeScreen={handlerChangeScreen}
            colorFoneCardInfo={schemaColorList.colorFoneCardInfo}
            colorText={schemaColorList.colorText}
            bgColor={schemaColorList.colorFoneBackground}
            toggleBgColor={schemaColorList.toggleBgColor}
            colorFoneBackground={schemaColorList.colorFoneBackground}
            colorFoneBackgroundElement={schemaColorList.colorFoneBackgroundElement}
          />
        </React.Fragment>
      ) : (
        <Spinner />
      )}

      {isLoading ? (
        <Spinner />
      ) : !listAplication.count ? (
        <CardApplication
          listAplication={listAplication}
          handlerDeleteApplication={handlerDeleteApplication}
          handlerChangeApplication={handlerChangeApplication}
          handlerInCatalogApplication={handlerInCatalogApplication}
        />
      ) : null}
    </SafeAreaView>
  );
};
export default CreateInSales;
